import React from 'react';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from 'react-google-maps';
import MapDirectionsRenderer from './MapDirectionsRenderer'

const Map = withScriptjs(withGoogleMap((props) => {
    var placeMarkers = [];
    var friendMarkers = [];
    console.log("is initial state?", !(props.hasOneDest || props.hasMultipleDest || props.hasFriends))
    console.log("is one destination? ", props.hasOneDest && !props.hasMultipleDest)
    if((props.hasOneDest || props.hasMultipleDest) && Object.keys(JSON.parse(props.destinations)).length){
        placeMarkers = props.hasOneDest ? <Marker position={JSON.parse(props.destinations).geometry.location}></Marker> : 
            JSON.parse(props.destinations).map( dest => <Marker
                key={dest.place_id}
                position={dest.geometry.location}
                //icon={props.destinations.length===1? } //fix this to change the icon based on store or restaurant etc.
            />);
    }
    if (props.hasFriends && props.friends.length){
        friendMarkers = props.friends.map( friend => {
            if(friend.location.length){
                return (
                <Marker
                    key={friend.id}
                    position={JSON.parse(friend.location).geometry.location}
                />)
            }
            return undefined
        });
    }
  
  return (
    
    <GoogleMap
      defaultZoom={12}
      center={{lat: 38.895, lng: -77.036}}
      >
      {!props.isInitialState && props.destinations.length && placeMarkers}
      {!props.isInitialState && props.hasFriends && friendMarkers }
      {!props.isInitialState && props.hasOneDest && props.hasFriends && <MapDirectionsRenderer
        dest={props.hasOneDest && JSON.parse(props.destinations).geometry.location}
        origins={props.hasFriends && props.friends.map(friend => {
            if(friend.location.length){
                return (
                    JSON.parse(friend.location).geometry.location
                )
            }
            return undefined
        })}
        // travelMode={google.maps.TravelMode.DRIVING} //default is driving anyway
      />}
    </GoogleMap>
    
  );

  }
))

export default Map
